import React, { Component } from 'react';

class ProjectItem extends Component {

	deleteProject(id){
		this.props.onDelete(id);
	}

	doneProject(id){
		this.props.onDone(id);
	}

	render() {
		console.log(this.props);
	return (
	  <li className="Project">
	    <span className="itemTitle">
		    <strong>{this.props.project.title}</strong>: {this.props.project.category}
	    </span>
		<span className="itemStatus">
			<a className="close" href="#" onClick={this.doneProject.bind(this, this.props.project.id)}>Done</a> | 
		    <a className="delete" href="#" onClick={this.deleteProject.bind(this, this.props.project.id)}>Delete</a>
	    </span>
	  </li>
	);
	}
}

ProjectItem.propTypes = {
	project: React.PropTypes.object,
	onDelete: React.PropTypes.func
}

export default ProjectItem;



